<template>
        <el-input v-model="coupon">
            <span slot="prepend" class="text-truncate">Discount</span>

            <el-button slot="append"
                       @click=verify
            >
                <vue-icon :name="icon" :class="{throbbing: (icon == 'zap')}"></vue-icon>
            </el-button>
        </el-input>
</template>

<script>
  import {mapFields} from 'vuex-map-fields';

  export default {

    data: function () {
      return {
        status: 'unverified'
      }
    },

    methods: {
      verify: function () {


        var broadcast = this.broadcast;
        var store = this.$store;
        var data = this.$data;
        data.status = 'zap';


        broadcast({type: 'click', from: 'coupon', event: 'attempting'})

        this.verifyDiscount(this.coupon)
          .then(function (response) {
            data.status = 'verified'
            store.dispatch("setDiscount", response.data);
            broadcast({type: 'click', from: 'coupon', event: 'succeeded'})
          }.bind(data).bind(store).bind(broadcast))
          .catch(function (response) {
            data.status = 'shield'
            broadcast({type: 'click', from: 'coupon', event: 'failed'})

            setTimeout(function () {
              data.status = 'unverified'
            }.bind(data), 3000);

          }.bind(data).bind(broadcast))
      }
    },

    watch: {
      coupon: function (fresh, stale) {
        if (fresh != stale) {
          this.status = 'unverified'
        }
      }
    },

    computed: {
      ...mapFields(['coupon', 'discount']),

      icon: function () {
        //return {verified: 'pencil', unverified: 'lock'}[this.status];
        return this.status
      }
    },

  }
</script>

<style>
    .throbbing {
        animation-name: throb;
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite;
        animation-duration: 0.75s;
        animation-direction: alternate;
    }

    @-webkit-keyframes throb {
        0% {
            opacity: 1.0;
        }
        100% {
            opacity: 0.25;
        }
    }

    @keyframes throb {
        0% {
            opacity: 1.0;
        }
        100% {
            opacity: 0.25;
        }
    }
</style>