<template>
    <div :class="breakName" v-show="visable">

        <span class="time">
          {{ bookable.time }}
          <small>{{ bookable.hint }}</small>
        </span>

        <template v-if="bookable.hint != 'expired'">

            <button
                    v-if="!_.isEmpty(bookable) && (_.isEmpty(selectedBookable) || selectedBookable.id != bookableId)"
                    :aria-label=arialLabel(bookable)
                    class="btn btn-outline-secondary book"
                    @click="setBookable(bookable); broadcast({type:'click', from:'bookable', event:'select'})"
            >
                <span class="label"></span>
                <small>{{ bookable.hint }}</small>
            </button>

            <button
                    v-if="!_.isEmpty(bookable) && !_.isEmpty(selectedBookable) && (selectedBookable.id == bookableId)"
                    :aria-label=arialLabel(bookable)
                    class="btn btn-outline-secondary book active"
                    @click="/*setBookable({}); */broadcast({type:'click', from:'bookable', event:'select'})"
            >
                <span class="label"></span>
                <small>{{ bookable.hint }}</small>
            </button>
        </template>

        <template v-if="bookable.hint == 'expired'">
            <span class="expired"><span class="label"></span> </span>
        </template>

    </div>
</template>

<script>
  import {mapState, mapActions, mapMutations, mapGetters} from "vuex";

  export default {
    props: ["bookable-id", "watch-date"],

    data: function () {
      return {
        uid: null,
        bookable: {}
      };
    },

    methods: {
      ...mapGetters(["getActiveDate"]),
      ...mapActions(["setBookable"]),

      arialLabel: function (bookable) {
        return 'book ' + bookable.time
      },

      loadBookableDetails: function () {
        let date = this.getActiveDate();
        let target_date = this.dateFormat(date, "YYYY-MM-DD");

        var data = this.$data;
        this.loadBookable(target_date, this.bookableId).then(
          function (result) {
            data.bookable = result.data;
          }.bind(data)
        );
      }
    },

    watch: {
      activeDate: function (fresh, stale) {
        if (typeof this.watchDate == "undefined" || this.watchDate) {
          this.loadBookableDetails();
        }
      }
    },

    computed: {
      ...mapState({
        selectedBookable: 'bookable',
        activeDate: function (state) {
          return state.activeDate;
        }
      }),

      breakName: function () {
        if (!_.isEmpty(this.bookable.name)) {
          return this.bookable.name.split(/[^A-Za-z]/).join(' ');
        }
        return '';
      },

      visable: function () {
        return (typeof this.bookable.id != "undefined");
      }
    },

    mounted: function () {
      this.uid = this._uid;
      this.$nextTick(this.loadBookableDetails);
    }
  };
</script>

<style>
    .btn small {
        display: block
    }

</style>
