<template>
  <div>
    <checkfront-bookable-output
      v-for="(data, index) in fields"
      v-bind:key="'output-' + index"
      v-bind:data="data"
      :field="JSON.stringify(data)"
    >
    </checkfront-bookable-output>

    <div
      v-for="(data, index) in fees"
      v-bind:key="'fees-' + index"
      v-bind:data="data"
    >
      <div v-if="data.quantity">
        <span>{{ data.quantity }}</span>
        <span> @ </span>
        <span>{{ data.label }}</span>
        <span>{{ data.tally | money }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapState } from "vuex";

export default {
  methods: {
    ...mapGetters([
      "getActiveDate",
      "getPricing",
      "getSubTotal",
      "getBillableHeadCount"
    ]),
    ...mapActions(["setReservationKeyVal", "tackOnFee"]),

    loadIncludedFees: function() {
      var quantity;
      var tackOnFee = this.tackOnFee;

      let date = this.getActiveDate();
      let target_date = this.dateFormat(date, "YYYY-MM-DD");

      for (var config of SYS.included) {
        switch (config["for"]) {
          case "guest":
            quantity = this.getBillableHeadCount();
            break;
          case "party":
            quantity = 1;
            break;
        }

        this.loadFee(target_date, config.id, quantity).then(
          function(response) {
            try {
              let fee = {
                id: response.data.id,
                quantity: quantity,
                label: response.data.name,
                tally: response.data.pricing[0]["price"] * quantity,
                field: response.data.pricing[0]["field"]
              };

              tackOnFee(fee);
            } catch (e) {}
          }
            .bind(quantity)
            .bind(tackOnFee)
        );
      }
    }
  },

  watch: {
    headCount: function() {
      this.loadIncludedFees();
    }
  },

  computed: {
    ...mapState(["reservation", "activeDate", "fees"]),
    headCount: function() {
      return this.getBillableHeadCount();
    },
    subTotal: function() {
      return this.getSubTotal();
    },
    fields: function() {
      let pricing = this.getPricing();
      let fields = [];

      for (var i = 0; i < pricing.length; i++) {
        fields[i] = {
          input: pricing[i].field,
          label: pricing[i].label,
          price: pricing[i].price
        };
      }

      return fields;
    }
  },
  mounted: function() {
    this.loadIncludedFees();
  },

  beforeMount: function() {
    for (var field of this.fields) {
      if (typeof this.reservation[field.input] == "undefined") {
        this.setReservationKeyVal({ field: field.input, val: 0 });
      }
    }
  }
};
</script>
