<template>
    <div v-if="visible">

        <checkfront-bookable-input
                v-for="(data, index) in fields"
                v-bind:key="index"
                v-bind:data="JSON.stringify(data)"
                :field="JSON.stringify(data)"
                :click-payload=JSON.stringify(data)
        ></checkfront-bookable-input>

        <div class="toomany" v-if="tooMany"><span></span></div>
    </div>
</template>

<script>
  import {mapGetters, mapState} from "vuex";

  export default {
    data: function () {
      return {
        keys: {}
      };
    },

    methods: {
      ...mapGetters(["getPricing", "getHeadCount"])
    },

    computed: {
      ...mapState(["bookable", "reservation"]),

      tooMany: function () {
        return (this.getHeadCount() > this.bookable.limit) ? true : false;
      },

      fields: function () {
        let pricing = this.getPricing();
        let fields = [];

        for (var i = 0; i < pricing.length; i++) {
          fields[i] = {
            input: pricing[i].field,
            label: pricing[i].label,
            price: pricing[i].price,
            sku: pricing[i].sku,
          };
        }

        return fields;
      },

      visible: function () {
        let pricing = this.getPricing();
        return !_.isEmpty(pricing) ? true : false;
      }
    }
  };
</script>

<style>
    .toomany {
        color: #990000;
    }
</style>