import Vue from "vue";
import Vuex from "vuex";
import VuexPersist from "vuex-persist";
import { getField, updateField } from "vuex-map-fields";
//import cloneDeep from "bootstrap-vue/esm/utils/clone-deep";
import _ from "lodash";

const vuexPersist = new VuexPersist({
  key: "checkfront-#CheckFrontTagsVersion#",
  storage: sessionStorage
});

Vue.use(Vuex);

export default new Vuex.Store({
  plugins: [vuexPersist.plugin],
  state: {
    fees: [],
    addOns: [],
    selectedAddOns: [],
    activeDate: new Date()
      .toString()
      .split("(")[0]
      .trim(),
    coupon: "",
    discount: {},
    bookables: [],
    bookable: {},
    reservation: {},
    form: {},

    cleared: 0
  },

  getters: {
    getField,

    getDiscountSubTotal: state => {
      var pricing = {};
      var prop;
      var total = 0;

      try {
        for (prop of state.bookable.pricing) {
          let amount = prop.price;
          let savings = 0;

          if (_.includes(state.discount.on, state.bookable.id)) {
            switch (state.discount.type) {
              case "percent":
                savings = amount * (state.discount.rate / 100);
                // I know this is stupid, but it's gotta match checkfront
                savings = savings.toFixed(2);
                break;

              case "fixed":
                savings = state.discount.rate;
                break;
            }
          }

          pricing[prop.field] = (amount - savings).toFixed(2);
        }

        for (prop in state.reservation) {
          if (state.reservation[prop] > 0) {
            total += state.reservation[prop] * pricing[prop];
          }
        }
      } catch (e) {}

      try {
        for (var addon of state.selectedAddOns) {
          total += addon.price * addon.quantity;
        }
      } catch (e) {}

      try {
        for (var fee of state.fees) {
          total += fee.tally;
        }
      } catch (e) {}

      return total;
    },

    getSubTotal: state => {
      var pricing = {};
      var prop;
      var total = 0;

      try {
        for (prop of state.bookable.pricing) {
          pricing[prop.field] = prop.price;
        }

        for (prop in state.reservation) {
          if (state.reservation[prop] > 0) {
            total += state.reservation[prop] * pricing[prop];
          }
        }
      } catch (e) {}

      try {
        for (var addon of state.selectedAddOns) {
          total += addon.price * addon.quantity;
        }
      } catch (e) {}

      try {
        for (var fee of state.fees) {
          total += fee.tally;
        }
      } catch (e) {}

      return total;
    },

    getHeadCount: state => {
      var total = 0;
      for (var prop in state.reservation) {
        total += state.reservation[prop];
      }

      return total;
    },

    getBillableHeadCount: state => {
      var pricing = {};
      var prop;
      var total = 0;

      try {
        for (prop of state.bookable.pricing) {
          pricing[prop.field] = prop.price;
        }

        for (prop in state.reservation) {
          if (state.reservation[prop] > 0) {
            if (_.isEmpty(pricing[prop])) {
              continue;
            }
            total += state.reservation[prop];
          }
        }
      } catch (e) {}

      return total;
    },

    getDiscountLevel: state => {
      return !_.isEmpty(state.discount) ? state.discount.level : "none";
    },
    getDiscountType: state => {
      return !_.isEmpty(state.discount) ? state.discount.type : "none";
    },
    getDiscountRate: state => {
      return !_.isEmpty(state.discount) ? Number(state.discount.rate) : 0;
    },

    getDiscountOn: state => {
      return !_.isEmpty(state.discount) ? state.discount.on : [];
    },

    getPricing: state => {
      if (state.bookable.pricing) {
        return state.bookable.pricing;
      }

      return [];
    },

    getActiveDate: state => {
      if (new Date(state.activeDate) < new Date(new Date().toDateString())) {
        state.activeDate = new Date().toString();
      }

      return state.activeDate;
    },
    getBookable: state => {
      return state.bookable;
    },

    getBrowserData: state => {
      return {
        activeDate: state.activeDate,
        bookable: state.bookable,
        selectedAddOns: state.selectedAddOns,
        reservation: state.reservation,
        form: state.form,
        discount: state.discount
      };
    }
  },
  mutations: {
    updateField,

    applyFees: function(state, payload) {
      state.fees = payload;
    },

    applyReset: function(state) {
      state.selectedAddOns = [];
      state.activeDate = new Date().toString();
      state.coupon = "";
      state.discount = {};
      state.bookable = {};

      var prop;
      for (prop in state.form) {
        state.form[prop] = "";
      }

      for (prop in state.reservation) {
        state.reservation[prop] = 0;
      }

      state.cleared++;
    },

    applyCoupon: (state, payload) => {
      state.coupon = payload;
    },

    applyFormVal: (state, payload) => {
      state.form[payload.field] = payload.val;
    },

    applyToReservation: (state, payload) => {
      if (payload.val >= 0) {
        state.reservation[payload.field] = payload.val;
      }
    },

    applyActiveDate: (state, date) => {
      if (typeof date == "object") {
        date = date.toString();
      }

      let data = {
        type: "state",
        from: "main-bus",
        event: "date-change",
        app: "tagApp"
      };
      window.parent.postMessage(JSON.stringify(data), "*");

      state.activeDate = date;
    },

    applyBookables: (state, payload) => {
      let categories = _.uniq(_.filter(_.map(payload, "category")));

      let still_valid = state.bookables.filter(
        function(item) {
          return !categories.includes(item.category);
        }.bind(categories)
      );

      state.bookables = still_valid.concat(payload);
    },

    applyBookable: (state, payload) => {
      state.bookable = payload;
    },

    applyDiscount: (state, payload) => {
      state.discount = payload;
    },

    applyAddOns: (state, payload) => {
      state.addOns = payload;
    },

    applySelectedAddOns: (state, payload) => {
      state.selectedAddOns = payload;
    },

    applyBrowserData: (state, payload) => {
      let keys = Object.keys(payload);
      for (let key of keys) {
        state[key] = payload[key];
      }
    }
  },
  actions: {
    restoreBrowserData: ({ commit, state }, payload) => {
      commit("applyBrowserData", payload);
    },

    tackOnFee: ({ commit, state }, fee) => {
      let fees = _.clone(state.fees);
      let found = false;

      for (let i in fees) {
        if (fees[i].id == fee.id) {
          fees[i] = fee;
          found = true;

          if (!fee.quantity) {
            fees.splice(i, 1);
          }
        }
      }

      if (!found) {
        fees.push(fee);
      }

      commit("applyFees", fees);
    },

    incAddon: ({ commit, state }, addonId) => {
      let selectedAddOns = _.clone(state.selectedAddOns);

      for (let addon of selectedAddOns) {
        if (addon.id == addonId) {
          addon.quantity++;
          commit("applySelectedAddOns", selectedAddOns);
          return;
        }
      }

      for (let addon of state.addOns) {
        if (addon.id == addonId) {
          let match = _.clone(addon);
          match["quantity"] = 1;
          selectedAddOns.push(match);
          commit("applySelectedAddOns", selectedAddOns);
          return;
        }
      }
    },

    decAddon: ({ commit, state }, addonId) => {
      let selectedAddOns = _.clone(state.selectedAddOns);

      selectedAddOns.forEach(
        function(item, index) {
          if (item.id != addonId) {
            return;
          }

          item.quantity--;
          if (item.quantity <= 0) {
            selectedAddOns.splice(index, 1);
          }
        }
          .bind(selectedAddOns)
          .bind(addonId)
      );

      commit("applySelectedAddOns", selectedAddOns);
    },
    clrAddon: ({ commit, state }, addonId) => {
      let selectedAddOns = _.clone(state.selectedAddOns);

      selectedAddOns.forEach(
        function(item, index) {
          if (item.id != addonId) {
            return;
          }
          selectedAddOns.splice(index, 1);
        }
          .bind(selectedAddOns)
          .bind(addonId)
      );

      commit("applySelectedAddOns", selectedAddOns);
    },

    inc: ({ commit, state }, field) => {
      let val = state.reservation[field];
      val++;
      commit("applyToReservation", { field, val });
    },
    dec: ({ commit, state }, field) => {
      let val = state.reservation[field];
      val--;
      commit("applyToReservation", { field, val });
    },
    clr: ({ commit, state }, field) => {
      commit("applyToReservation", { field, val: 0 });
    },

    setFormVals: ({ commit, state }, payload) => {
      for (var property in payload) {
        if (!payload.hasOwnProperty(property)) continue;

        commit("applyFormVal", { field: property, val: payload[property] });
      }
    },

    setDiscount: ({ commit, state }, payload) => {
      commit("applyDiscount", payload);
    },

    reset: ({ commit, state }, payload) => {
      commit("applyReset");
    },

    clearDiscount: ({ commit, state }, payload) => {
      commit("applyDiscount", {});
      commit("applyCoupon", "");
    },

    setReservationKeyVal: ({ commit, state }, payload) => {
      commit("applyToReservation", payload);
    },

    setActiveDate: ({ commit, state }, date) => {
      if (typeof date == "object") {
        date = SYS.Vue.moment(date).toString();
      }

      commit("applyActiveDate", date);
    },

    setBookables: ({ commit, state }, payload) => {
      commit("applyBookables", payload);

      var found = false;
      if (typeof state.bookable.id != "undefined") {
        for (var i = 0; i < state.bookables.length; i++) {
          if (state.bookables[i].id == state.bookable.id) {
            found = true;
            break;
          }
        }
      }

      if (!found) {
        commit("applyBookable", {});
      }
    },

    setBookable: ({ commit, state }, payload) => {
      if (typeof payload.pricing != "undefined") {
        for (var pricing of payload.pricing) {
          if (typeof state.reservation[pricing.field] == "undefined") {
            let obj = {};
            obj[pricing.field] = 0;
            state.reservation = { ...state.reservation, ...obj };
          }
        }
      }

      commit("applyBookable", payload);
    },

    setAddOns: ({ commit, state }, payload) => {
      commit("applyAddOns", payload);
    }
  }
});
