<template>
    <div class="savings" v-if="!_.isEmpty(discount)">
        <span class="label">{{ coupon }}</span>
         <span class="price">{{ (getSubTotal() - getDiscountSubTotal()) | money }}</span>
    </div>
</template>

<script>
  import {mapState, mapGetters} from "vuex";

  export default {

    methods:{
      ...mapGetters(["getSubTotal", "getDiscountSubTotal"])
    },

    computed: {
      ...mapState(["coupon", "discount"]),
    }
  };
</script>
