<template>
    <el-input
            readonly
            required
            type="number"
            width="2"
            value="0"
            min="0"
            v-model="input"
    >
    <span slot="prepend">
      <span class="text-truncate label">{{ addOn.label }}</span>
      <span class="price">{{ addOn.price | money }}</span>
    </span>

        <el-button slot="append" @click="incAddon(addOn.id); broadcast({type:'click', from:'addon', event: 'inc', payload:addOn})">
            <vue-icon name="plus"></vue-icon>
        </el-button>

        <el-button slot="append" @click="decAddon(addOn.id); broadcast({type:'click', from:'addon', event: 'dec', payload:addOn})">
            <vue-icon name="dash"></vue-icon>
        </el-button>

        <el-button slot="append" @click="clrAddon(addOn.id); broadcast({type:'click', from:'addon', event: 'clear', payload:addOn})">
            <vue-icon name="circle-slash"></vue-icon>
        </el-button>
    </el-input>
</template>

<script>
  import {mapState, mapActions, mapMutations, mapGetters} from "vuex";

  export default {
    props: ['addonId'],

    methods: {
      ...mapActions(['setAddOns', 'incAddon', 'decAddon', 'clrAddon']),
    },

    computed: {
      ...mapState(["addOns", "selectedAddOns"]),

      addOn: function () {
        for (let item of this.addOns) {
          if (item.id == this.addonId) {
            return item;
          }
        }

        return null;
      },

      input: {
        get: function () {

          for (let item of this.selectedAddOns) {
            if (item.id == this.addonId) {
              return item.quantity;
            }
          }

          return 0;
        },

        set: function (value) {
          console.log('should never get hit')
        }
      },

    }
  }
</script>