<template>
    <div>
    <checkfront-addon
            v-for="(data, index) in addOns"
            v-bind:key="index"
            v-bind:data=data
            :addon-id=data.id :key=data.id></checkfront-addon>


    </div>
</template>

<script>
  import {mapState, mapActions, mapMutations, mapGetters} from "vuex";

  export default {
    props: ["categoryId"],

    data: function () {
      return {
        uid: null
      };
    },

    methods: {
      ...mapGetters(["getActiveDate"]),

      loadAddOnsFromServer: function () {
        let date = this.getActiveDate();
        date = this.dateFormat(date, "YYYY-MM-DD")

        var store = this.$store;
        this.loadAddOns(date, this.categoryId).then(
          function (response) {
            store.dispatch("setAddOns", response.data);
          }.bind(store)
        );
      },

    },

    computed: {
      ...mapState(['addOns']),
    },

    mounted: function () {
      this.uid = this._uid;
      this.loadAddOnsFromServer()
    }
  };
</script>
