<template>
  <div :data-uid="uid">
    <div class="nprogress-parent"></div>
    <datepicker
      :inline="true"
      :highlighted="highlighted()"
      :value="activeDate"
      :disabled-dates="{
        to: new Date(new Date().valueOf() - 1000 * 60 * 60 * 24)
      }"
      @changedMonth="changeMonth"
      @selected="setActiveDate"
    ></datepicker>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations, mapGetters } from "vuex";
import Datepicker from "vuejs-datepicker";

export default {
  components: { Datepicker },
  props: ["categoryIds"],
  data: function() {
    return {
      days: [],
      uid: null
    };
  },
  computed: {
    activeDate: function() {
      return this.getActiveDate();
    }
  },
  methods: {
    ...mapGetters(["getActiveDate"]),
    ...mapActions(["setActiveDate"]),

    changeMonth: function(date) {
      if (typeof date == "object" && typeof date.timestamp == "number") {
        date = new Date(date.timestamp);
      }

      this.updateHighlights(date);
    },

    updateHighlights: function(date) {
      date = !date ? this.startOfMonth(this.activeDate) : date;

      this.days = [];
      let data = this.$data;

      let start_date = this.startOfMonth(date);
      start_date = this.dateFormat(start_date, "YYYY-MM-DD");

      let end_date = this.endOfMonth(date);
      end_date = this.dateFormat(end_date, "YYYY-MM-DD");

      this.loadDates(start_date, end_date, JSON.parse(this.categoryIds)).then(
        function(result) {
          data.days = result.data;
        }.bind(data)
      );
    },

    highlighted: function() {
      return { daysOfMonth: this.days };
    }
  },
  mounted() {
    this.uid = this._uid;
    this.$nextTick(this.updateHighlights);
  }
};
</script>

<style>
.vdp-datepicker__calendar {
  /* default was 300px */
  width: auto !important;
}
</style>
