<template>
    <div :data-uid="uid">
        <form-wizard v-if="visible"
                     @on-complete="submit"
                     :color="color"
                     :step-size="stepSize"
                     title=""
                     subtitle=""
                     finishButtonText="Submit"
                     ref="wizard"
                     @on-change="function(prevIndex, nextIndex){ broadcast({type:'click', from: 'wizard', event:'step', payload: {direction:((prevIndex < nextIndex)? 'forward':'backward'), was: prevIndex+1, is: nextIndex+1}});}"
        >
            <div v-if=!_.isEmpty(msg)
                 class="alert"
                 :class="msgClass"
                 v-html="msg"></div>

            <tab-content v-for="(data, index) in tabs"
                         v-bind:key="index"
                         v-bind:data="data"
                         :title="data.title"
                         :before-change="function(){ return validate('form-ref-' + index, _.pick(rules, data.fields)); }"
            >

                <div v-if=_.isEmpty(msg)
                     class="alert alert-dark"
                     v-html="data.instruction"></div>

                <el-form :model="datum"
                         :ref="'form-ref-' + index"
                         :rules=currentStepRules
                >

                    <el-form-item v-for="(field, pointer) in data.fields"
                                  v-bind:key="pointer"
                                  v-bind:data="field"
                                  :prop="field"
                    >
                        <div v-if="conditionsCleared(field, data.fieldConditions)">

                            <el-input v-if="getDefinition(field, 'type') == 'text'"
                                      :key=loaded
                                      v-model="datum[field]">
                                <template slot="prepend">
                                    <span class="text-truncate">{{ getDefinition(field, 'label') }}</span>
                                </template>
                            </el-input>


                            <div v-if="getDefinition(field, 'type') == 'textarea'"
                                 class="text-truncate">{{ getDefinition(field, 'label') }}
                            </div>
                            <el-input v-if="getDefinition(field, 'type') == 'textarea'"
                                      type="textarea"
                                      :rows="4"
                                      v-model="datum[field]">
                            </el-input>


                            <div v-if="getDefinition(field, 'type') == 'select' && !['customer_region'].includes(field)"
                                 class="text-truncate">{{ getDefinition(field, 'label') }}
                            </div>
                            <el-select v-if="getDefinition(field, 'type') == 'select' && !['customer_region'].includes(field)"
                                       placeholder="Select..."
                                       v-model="datum[field]"
                            >
                                <el-option
                                        v-for="(item, key) in getDefinition(field, 'options')"
                                        :key="key"
                                        :label="item"
                                        :value="item">
                                </el-option>
                            </el-select>

                            <el-select v-if="field == 'customer_region' && showRegionMenu"
                                       placeholder="Select..."
                                       v-model="datum[field]"
                            >

                                <el-option
                                        v-for="item in regions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>

                            </el-select>

                            <el-checkbox v-if="getDefinition(field, 'type') == 'checkbox'" v-model="datum[field]">{{
                                getDefinition(field, 'label') }}
                            </el-checkbox>

                        </div>
                    </el-form-item>

                </el-form>

            </tab-content>

            <!--AddOns-->
            <tab-content v-for="(data, index) in addOnsConfig"
                         v-bind:key="'i'+ index"
                         v-bind:data="data"
                         :title="data.title"
            >
                <checkfront-addons :category-id=data.category></checkfront-addons>
            </tab-content>


        </form-wizard>
    </div>
</template>

<script>
  import {mapState, mapActions, mapMutations, mapGetters} from "vuex";

  export default {
    props: {
      steps: {
        default: [
          {
            title: "Oops...",
            instruction: "Wizard configuration is missing.",
            fields: []
          }
        ]
      },

      color: {default: "#808080"},
      stepSize: {default: "xs"}
    },

    data: function () {
      return {
        uid: null,
        regions:[],

        fields: [],
        definitions: {},
        rules: {},
        stepRules: {},
        datum: {},
        msg: '',
        msgClass: {'alert-dark': true},

        count: 0,
        loaded: 0,
      };
    },

    computed: {
      ...mapState(["bookable", "reservation", "selectedAddOns", "form", "discount", "coupon", "cleared", "fees"]),

      addOnsConfig: function () {
        return SYS.wizardAddOns;
      },

      currentStepRules: function () {
        return this.stepRules;
      },

      tabs: function () {
        if (typeof this.steps == "string") {
          return SYS[this.steps];
        }
        return this.steps;
      },

      visible: function () {
        return (this.getHeadCount && !_.isEmpty(this.definitions)) ? true : false;
      },

      showRegionMenu: function () {
        return (!this._.isEmpty(this.datum.customer_country))? true:false;
      },

      country: function () {
        return this.datum.customer_country;
      }
    },

    watch: {
      country: function(){
        var data = this.$data;
        let country_code = this._.invert(this.getDefinition('customer_country', 'options'))[this.country];

        this.loadRegions(country_code)
          .then(function (response) {
            data.regions = response.data;
          }.bind(data));
      },

      cleared: function () {
        this.datum = {};
        this.msg = '';
        this.msgClass = {'alert-dark': true};
        this.loadBookingForm();

        this.$refs['wizard'].reset();
      },

      datum: {
        handler(val) {
          var last = this.form;

          var diff = this._.omitBy(val, function (v, k) {
            return last[k] === v;
          });

          this.setFormVals(diff);
        },
        deep: true
      }
    },

    methods: {
      ...mapActions(["setFormVals", "clearDiscount", "reset"]),
      ...mapGetters(["getActiveDate", "getHeadCount"]),

      loadRegionsFromServer: function(){

      },

      conditionsCleared: function (field, conditions) {

        if (typeof conditions == "undefined") {
          return true;
        }

        if (typeof conditions[field] == "undefined") {
          return true;
        }

        let f = conditions[field][0];
        let v = conditions[field][1];
        if (this.form[f] == v) {
          return true;
        }

        return false;
      },

      validate: function (ref, rules) {
        this.stepRules = rules;

        return new Promise((resolve, reject) => {
          this.$nextTick(() => {
            resolve(this.$refs[ref][0].validate())
          })
        })
      },

      getDefinition(field, prop) {
        if (_.isEmpty(this.definitions) || _.isEmpty(this.definitions[field]) || _.isEmpty(this.definitions[field][prop])) {
          return null
        }

        return this.definitions[field][prop]
      },

      submit: function () {
        var broadcast = this.broadcast;

        let date = this.getActiveDate();
        date = this.dateFormat(date, "YYYY-MM-DD")

        let post = {
          date: date,
          details: this.bookable.id,
          quantities: this.reservation,
          addons: this.selectedAddOns,
          fees: this.fees,
          form: this.form
        }

        if (!_.isEmpty(this.discount)) {
          post['discount'] = this.coupon;
        }

        var data = this.$data
        data.msg = 'Sending...'
        data.msgClass = {'alert-primary': true}

        var clearDiscount = this.clearDiscount;

        this.submitBooking(post)
          .then(function (response) {
            data.msg = '<p>On Hold: ' + response.data.confirmation + '</p>';
            data.msg += '<a href="' + response.data.payment_url + '" target="_blank">Please click here to confirm by credit card.</a>'
            data.msgClass = {'alert-success': true}

            broadcast({
              type: 'response',
              from: 'api',
              event: 'booking',
              payload: {code: response.data.confirmation, url: response.data.payment_url}
            })

          }.bind(data).bind(broadcast))
          .catch(function (error) {
            try {
              data.msg = error.response.data.message;
              if (data.msg == 'Discount not applicable.  Coupon has been removed.  Please submit again if you wish to proceed.') {
                clearDiscount()
              }
            } catch (e) {
              data.msg = 'An error has occurred.'
            }
            data.msgClass = {'alert-danger': true}

            broadcast({type: 'response', from: 'api', event: 'error', payload: {msg: data.msg}})
          }.bind(data).bind(clearDiscount).bind(broadcast))
      }
    },

    mounted: function () {
      this.uid = this._uid;

      var data = this.$data;
      var form = this.form;

      this.loadBookingForm().then(
        function (response) {
          let validators = _.filter(_.map(SYS.wizard, 'validators'));

          data.definitions = response.data.definitions;
          data.rules = response.data.rules;

          for (let validator of validators) {
            for (let field_name in validator) {
              if (_.isEmpty(data.rules[field_name])) {
                data.rules[field_name] = []
              }

              data.rules[field_name] = data.rules[field_name].concat(validator[field_name])
            }
          }
          var property;
          for (property in data.definitions) {
            if (!data.definitions.hasOwnProperty(property)) continue;

            if (typeof form[property] == "undefined") {
              form[property] = '';
            }
          }


          for (property in form) {
            if (!form.hasOwnProperty(property)) continue;
            data.datum = {..._.clone(form)};
          }

        }.bind(data).bind(form)
      );
    }
  };
</script>

<style>
    /* hides error msg */
    .el-form-item {
        margin-bottom: auto;
    }

    .el-form-item__error {
        display: none;
    }

    .vue-form-wizard .wizard-tab-content,
    .vue-form-wizard .wizard-card-footer {
        padding: 0px;
    }
</style>