<template>
    <div class="total">
        <span class="label"></span>
        <span class="price">{{ subTotal | money }}</span>
    </div>
</template>

<script>
  import {mapGetters, mapActions, mapState} from "vuex";

  export default {
    methods:{
      ...mapGetters(["getSubTotal"])
    },

    computed: {
      subTotal: function () {
        return this.getSubTotal();
      },
    }
  }
</script>