import store from "./store";

store.watch(
  function(state) {
    return state.bookable;
  },
  function(fresh, stale) {
    let data = {
      type: "state",
      from: "main-bus",
      event: null,
      app: "tagApp"
    };

    if (
      typeof stale.pricing == "undefined" &&
      typeof fresh.pricing != "undefined"
    ) {
      data.event = "bookable-added";
      window.parent.postMessage(JSON.stringify(data), "*");
      return;
    }

    if (typeof stale.id != "undefined" && typeof fresh.id == "undefined") {
      data.event = "bookable-removed";
      window.parent.postMessage(JSON.stringify(data), "*");
      return;
    }

    if (
      typeof stale.id != "undefined" &&
      typeof fresh.id != "undefined" &&
      stale.id != fresh.id
    ) {
      data.event = "bookable-changed";
      window.parent.postMessage(JSON.stringify(data), "*");
      //return;

      if (!basicallyTheSame(fresh.pricing, stale.pricing)) {
        data.event = "price-change-detected";
        window.parent.postMessage(JSON.stringify(data), "*");
      }
    }
  }
);

