<template>
  <b-list-group :data-uid="uid">
    <b-list-group-item
      v-for="(data, index) in categoryBookables"
      v-bind:key="index"
      v-bind:data=JSON.stringify(data)
    >
      <checkfront-bookable :bookable-id=data.id :key="data.id + data.stamp" watch-date=false></checkfront-bookable>
    </b-list-group-item>

    <b-list-group-item v-if="ready && _.isEmpty(categoryBookables)">
      <p class="soldout alert alert-danger"><span class="label"></span></p>
    </b-list-group-item>

  </b-list-group>
</template>

<script>
import { mapState, mapActions, mapMutations, mapGetters } from "vuex";

export default {
  props: ["categoryIds"],

  data: function() {
    return {
      uid: null,
      ready: false
    };
  },

  methods: {
    ...mapGetters(["getActiveDate"]),

    loadBookablesForDate: function(date) {

      var data = this.$data;
      var store = this.$store;
      var target_date = this.dateFormat(date, "YYYY-MM-DD");

      this.loadBookables(target_date, JSON.parse(this.categoryIds)).then(
        function(response) {
          store.dispatch("setBookables", response.data);
          data.ready = true;
        }.bind(store).bind(data)
      );
    },

    stringify: function (data) {
      return JSON.stringify(data)
    }
  },

  computed: {
    ...mapState(["bookables","activeDate"]),

    categoryBookables: function () {
      var category_ids = JSON.parse(this.categoryIds);

      return this.bookables.filter(function (bookable) {
        return category_ids.includes(bookable.category);
      }.bind(category_ids));
    }
  },

  watch: {
    activeDate(newValue, oldValue) {
      this.loadBookablesForDate(newValue);
    }
  },

  mounted: function() {
    this.uid = this._uid;
    var date = this.getActiveDate()
    this.$nextTick(function(){this.loadBookablesForDate(date)})
  }
};
</script>
