<template>
  <div>{{ activeDate | moment(format) }}</div>

</template>

<script>
import { mapGetters } from "vuex";

export default {
  props: ["format"],
  computed: {
    activeDate: function() {
      return this.getActiveDate();
    }
  },
  methods: {
    ...mapGetters(["getActiveDate"])
  }
};
</script>
