<template>
  <div>
    <button
      class="btn btn-outline-primary save"
      @click="save"
      v-if="_.isEmpty(hash)"
    >
      <span class="label"></span>
    </button>

    <button
      class="btn btn-outline-primary copy"
      @click="doCopy"
      v-if="!_.isEmpty(hash)"
    >
      <span class="label" v-if="_.isEmpty(error)"></span>
      <span class="hash" v-if="!_.isEmpty(error)">{{ hash }}</span>
    </button>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  data: function() {
    return {
      hash: null,
      error: false
    };
  },

  mounted: function() {
    var lookup = this.getUrlParam("lookup");
    var restoreBrowserData = this.restoreBrowserData;
    var broadcast = this.broadcast;

    if (!this._.isEmpty(lookup)) {
      this.fetchBrowserData(lookup).then(
        function(response) {
          restoreBrowserData(response.data);
          broadcast({
            type: "response",
            from: "lookup",
            event: "success",
            payload: response.data
          });
        }.bind(restoreBrowserData).bind(broadcast)
      ).catch(function (response) {
        broadcast({
          type: "response",
          from: "lookup",
          event: "error",
          payload: response
        }.bind(broadcast));
      });
    }
  },

  methods: {
    ...mapGetters(["getBrowserData"]),
    ...mapActions(["restoreBrowserData"]),

    save: function() {
      let browser_data = this.getBrowserData();
      var data = this.$data;
      var broadcast = this.broadcast;

      this.saveBrowserData(browser_data).then(
        function(response) {
          data["hash"] = response.data.id;
          broadcast({
            type: "response",
            from: "api",
            event: "success",
            payload: response.data
          });
        }
          .bind(data)
          .bind(broadcast)
      );
    },

    doCopy: function() {
      var data = this.$data;
      var broadcast = this.broadcast;
      let clipping = window.location.origin + "?lookup=" + this.hash;

      this.$copyText(clipping).then(
        function(e) {
          broadcast({
            type: "click",
            from: "browser-data",
            event: "copy",
            payload: { hash: data.hash }
          });
          data.hash = null;
        }
          .bind(data)
          .bind(broadcast),
        function(e) {
          alert("Please copy manually.");
          data.error = true;
        }.bind(data)
      );
    }
  }
};
</script>
