import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import buildUrl from "build-url";

import "nprogress/nprogress.css";
import NProgress from "nprogress";
const queryString = require('query-string');

NProgress.configure({
  showSpinner: false,
  trickleRate: 0.1,
  trickleSpeed: 400
});

let count = 0;

let SERVER_URL =
  process.env.NODE_ENV === "production"
    ? "https://CheckFront.theBackOffice.io"
    : "http://CheckFront.theBackOffice.test";

SERVER_URL += "/public-api";

// Add a request interceptor
axios.interceptors.request.use(
  function(config) {
    if (!count) {
      NProgress.start();
    } else if (count == 1) {
      NProgress.done(true);
      NProgress.configure({ parent: "body" });
      NProgress.start();
    }
    count++;
    return config;
  },
  function(error) {
    return Promise.reject(error);
  }
);

axios.interceptors.response.use(
  function(response) {
    count--;
    if (!count) {
      NProgress.done(true);
    }
    return response;
  },
  function(error) {
    count--;
    if (!count) {
      NProgress.done(true);
    }
    return Promise.reject(error);
  }
);

Vue.use(VueAxios, axios);

export default {
  install(Vue, opts) {
    Vue.mixin({
      methods: {
        loadAddOns(date, category_id) {
          this.loadingFx();

          let endpoint = buildUrl(SERVER_URL, {
            path: "addons?category_id={category}&date={date}"
              .replace("{category}", category_id)
              .replace("{date}", date)
          });

          return axios.get(endpoint);
        },

        verifyDiscount(discount_id) {
          this.loadingFx();

          let endpoint = buildUrl(SERVER_URL, {
            path: "discounts/{discount}".replace("{discount}", discount_id)
          });

          return axios.get(endpoint);
        },

        submitBooking(data) {
          this.loadingFx();

          let endpoint = buildUrl(SERVER_URL, {
            path: "bookings"
          });

          return axios.post(endpoint, data);
        },

        loadBookingForm() {
          this.loadingFx();

          let endpoint = buildUrl(SERVER_URL, {
            path: "bookings/create",
            queryParams: {}
          });

          return axios.get(endpoint);
        },

        loadBookingPolicy() {
          this.loadingFx();

          let endpoint = buildUrl(SERVER_URL, {
            path: "bookings/create",
            queryParams: {
              display: "policy"
            }
          });

          return axios.get(endpoint);
        },

        loadingFx: function() {
          if (this.$el.getAttribute("data-uid")) {
            NProgress.configure({
              parent: '[data-uid="' + this.$el.getAttribute("data-uid") + '"]'
            });
          } else {
            NProgress.configure({ parent: "body" });
          }
        },

        loadBookables: function(target_date, category_ids) {
          this.loadingFx();

          let endpoint = buildUrl(SERVER_URL, {
            path: "/calendar/dates/{date}".replace("{date}", target_date),
            disableCSV:true,
            queryParams: {
              'category_ids[]': category_ids
            }
          });
          return axios.get(endpoint);
        },

        loadBookable: function(target_date, bookable_id, quantity) {
          this.loadingFx();

          quantity = typeof quantity == "undefined" ? 1 : quantity;

          let endpoint = buildUrl(SERVER_URL, {
            path: "/calendar/dates/{date}/{details}"
              .replace("{date}", target_date)
              .replace("{details}", bookable_id),
            queryParams: {
              quantity
            }
          });
          return axios.get(endpoint);
        },

        loadFee: function(target_date, bookable_id, quantity) {
          this.loadingFx();

          let endpoint = buildUrl(SERVER_URL, {
            path: "/ancillary/fee",
            queryParams: {
              date: target_date,
              details: bookable_id,
              quantity
            }
          });
          return axios.get(endpoint);
        },

        loadDates: function(start_date, end_date, category_ids) {

          this.loadingFx();
          let endpoint = buildUrl(SERVER_URL, {
            path: "/calendar/dates",
            disableCSV:true,
            queryParams: {
              start_date,
              end_date,
              'category_ids[]': category_ids
            }
          });

          return axios.get(endpoint);
        },

        fetchBrowserData: function(lookup) {
          this.loadingFx();

          let endpoint = buildUrl(SERVER_URL, {
            path: "/browser-data/{browser_datum}".replace(
              "{browser_datum}",
              lookup
            )
          });

          return axios.get(endpoint);
        },

        saveBrowserData: function(data) {
          this.loadingFx();

          let endpoint = buildUrl(SERVER_URL, {
            path: "browser-data"
          });

          return axios.post(endpoint, data);
        },

        loadRegions: function(country) {
          this.loadingFx();
          let endpoint = buildUrl(SERVER_URL, {
            path: "/ancillary/regions?country={country}".replace(
              "{country}",
              country
            )
          });
          return axios.get(endpoint);
        }
      }
    });
  }
};
