<template>
  <div v-if="input">
    <span>{{ input }}</span>
    <span> @ </span>
    <span>{{ config.label }}</span>
    <span>{{ tally | money }}</span>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations, mapGetters } from "vuex";

export default {
  props: ["field"],

  beforeCreate: function() {},

  computed: {
    ...mapState(["reservation"]),
    input: {
      get: function() {
        return this.reservation[this.config.input];
      },
      set: function(value) {
        //this.setReservationKeyVal({field: this.config.input, val:value})
        console.log("Not set directly");
      }
    },

    tally: function() {
      return this.input * this.config.price;
    },

    config: function() {
      let config = JSON.parse(this.field);
      config.price = Number(config.price);
      return config;
    }
  },

  methods: {
    ...mapActions(["setReservationKeyVal", "inc", "dec", "clr"])
  }
};
</script>

<style>
.bookable-input-label {
  display: inline-block;
}
</style>
