<template>
    <div class="promo" :data-uid="uid">

        <button :aria-label="arialLabel"
                v-if="visible"
                class="btn btn-outline-secondary book"
                :class="{active: isSelected}"
                @click="selectBookable(); broadcast({type:'click', from:'promo', event:'select'});"
        >
            <span class="label"></span>
            <small>{{ bookable.hint }}</small>
        </button>

        <div>{{ date | moment(format) }}</div>
        <small>{{ bookable.hint }}</small>
        <div class="price"
             v-for="(price, index) in pricing"
             v-bind:key="index"
             v-bind:data="price"
        >
            <div>
                <span>{{ price.label }}</span>
                <span>{{ price.price }}</span>
            </div>
        </div>

        <div v-show="soldout" class="soldout"><span class="label"></span> </div>

    </div>
</template>

<script>
  import {mapState, mapActions, mapMutations, mapGetters} from "vuex";

  export default {
    props: {
      date: {
        default: () => {
          return new Date().toString()
        }
      },
      format: {
        default: "dddd, MMMM Do"
      },
      bookableId: {
        type: Number,
        required: true
      },
    },

    data: function () {
      return {
        uid: null,
        bookable: {},
        soldout: false
      };
    },

    methods: {
      ...mapGetters(['getActiveDate']),
      ...mapActions(['setBookable', 'setActiveDate']),


      selectBookable: function () {

        let date = SYS.Vue.moment(this.date);

        this.setBookable(this.bookable);
        this.setActiveDate(date);
      },

      loadBookableDetails: function () {
        let data = this.$data;
        var target_date = this.dateFormat(this.date, "YYYY-MM-DD");

        this.loadBookable(target_date, this.bookableId).then(
          function (result) {
            if(!_.isEmpty(result.data)){
                data.bookable = result.data;
            }else{
              data.soldout = true
            }
          }.bind(data)
        );
      }
    },

    computed: {
      ...mapState({
        selectedBookable: 'bookable',
        activeDate: function (state) {
          return state.activeDate;
        }
      }),

      arialLabel: function () {
        return 'book promotion' + this.dateFormat(this.date, "YYYY-MM-DD") +' at '+  this.bookable.time
      },

      visible: function () {
        return (!this._.isEmpty(this.bookable))
      },

      isSelected: function () {
        let date1 = SYS.Vue.moment(this.activeDate).toString(),
          date2 = SYS.Vue.moment(this.date).toString();

        return (!_.isEmpty(this.bookable) && !_.isEmpty(this.selectedBookable) && this.selectedBookable.id == this.bookableId && date1 == date2) ? true : false;
      },

      pricing: function () {
        return this.bookable.pricing;
      }
    },

    mounted: function () {
      this.uid = this._uid;
      this.$nextTick(this.loadBookableDetails);
    }
  };
</script>
