<template>
  <el-input
    readonly
    required
    type="number"
    width="2"
    value="0"
    min="0"
    :name="config.input"
    v-model="input"
  >
    <span slot="prepend">
      <span class="text-truncate label">{{ config.label }}</span>
      <span class="price">{{ config.price | money }}</span>
    </span>

    <el-button slot="append" @click="inc(config.input); broadcast({type:'click', from:'bookable-input', event:'increment', payload:decodedClickPayload})">
      <vue-icon name="plus"></vue-icon>
    </el-button>

    <el-button slot="append" @click="dec(config.input); broadcast({type:'click', from:'bookable-input', event:'decrement', payload:decodedClickPayload})">
      <vue-icon name="dash"></vue-icon>
    </el-button>

    <el-button slot="append" @click="clr(config.input); broadcast({type:'click', from:'bookable-input', event:'clear', payload:decodedClickPayload})">
      <vue-icon name="circle-slash"></vue-icon>
    </el-button>
  </el-input>
</template>

<script>
import { mapState, mapActions, mapMutations, mapGetters } from "vuex";

export default {
  props: ["field"],

  computed: {
    ...mapState(["reservation"]),

    decodedClickPayload: function(){
      return this.config;
    },

    input: {
      get: function() {
        return this.reservation[this.config.input];
      },
      set: function(value) {
        console.log("Should not be set directly.");
      }
    },

    config: function() {
      let config = JSON.parse(this.field);
      config.price = Number(config.price);
      return config;
    }
  },

  methods: {
    ...mapActions(["inc", "dec", "clr"])
  }
};
</script>

<style>
.el-input-group {
  margin-bottom: 7px;
}
</style>
