<template>
  <div v-html="policy"></div>
</template>

<script>
export default {
  data: function() {
    return {
      policy: "<div class='alert alert-info'>Loading</div>"
    };
  },

  mounted: function() {
    var data = this.$data;
    this.loadBookingPolicy().then(
      function(response) {
        data.policy = response.data.body;
      }.bind(data)
    );
  }
};
</script>
