<template>
    <span v-if="visible">{{ value }}</span>
</template>

<script>

  export default {

    props:['store-address'],
    
    computed:{
      visible: function () {
        return (!_.isEmpty(this.value))? true: false
      },

      value: function () {
        return _.get(this.$store.state, this.storeAddress)
      }
    }
    
  }
</script>