const Helpers = {
  install(Vue, opts) {
    Vue.mixin({
      methods: {

        getUrlParam: function(name){
          let uri = window.location.search.substring(1);
          let params = new URLSearchParams(uri);;

          return (params.has(name))? params.get(name) : undefined;
        },

        broadcast: function (data) {
          data['app'] = 'tagApp';

          window.parent.postMessage(JSON.stringify(data), '*');
        },

        /**
         * Returns a date set to the begining of the month
         *
         * @param {Date} date
         * @returns {Date}
         */
        startOfMonth: function (date) {


          if (typeof date == 'string') {
            date = new Date(date)
          }

          date.setDate(1);
          date.setHours(0);
          date.setMinutes(0);
          date.setSeconds(0);
          return date;
        },

        /**
         * Returns a date set to the end of the month
         *
         * @param {Date} date
         * @returns {Date}
         */
        endOfMonth: function (date) {

          if (typeof date == 'string') {
            date = new Date(date)
          }

          date.setMonth(date.getMonth() + 1);
          date.setDate(0);
          date.setHours(23);
          date.setMinutes(59);
          date.setSeconds(59);
          return date;
        },

        dateFormat: function (date, format) {

          if (typeof date == 'string') {
            date = new Date(date)
          }

          return SYS.Vue.moment(date).format(format)

          /*let parts = {
            Y: date.getFullYear().toString(),
            y: ("00" + (date.getYear() - 100)).toString().slice(-2),
            m: ("0" + (date.getMonth() + 1)).toString().slice(-2),
            n: (date.getMonth() + 1).toString(),
            d: ("0" + date.getDate()).toString().slice(-2),
            j: date.getDate().toString(),
            H: ("0" + date.getHours()).toString().slice(-2),
            G: date.getHours().toString(),
            i: ("0" + date.getMinutes()).toString().slice(-2),
            s: ("0" + date.getSeconds()).toString().slice(-2)
          };

          let modifiers = Object.keys(parts).join("");
          let reDate = new RegExp("(?<!\\\\)[" + modifiers + "]", "g");
          let reEscape = new RegExp("\\\\([" + modifiers + "])", "g");

          return format
            .replace(reDate, $0 => parts[$0])
            .replace(reEscape, ($0, $1) => $1);*/
        },

        isUrl: function (str) {
          if (typeof str !== "string") {
            return false;
          }

          var pattern = new RegExp(
            "^(https?:\\/\\/)?" + // protocol
            "((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|" + // domain name
            "((\\d{1,3}\\.){3}\\d{1,3}))" + // OR ip (v4) address
            "(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*" + // port and path
            "(\\?[;&a-z\\d%_.~+=-]*)?" + // query string
            "(\\#[-a-z\\d_]*)?$",
            "i"
          ); // fragment locator

          return !!pattern.test(str);
        }
      }
    });
  }
};

export default Helpers;
